.carousel-root {
    width:65vw;
    height: auto;
    margin: 1vh auto;
    position: relative;
}

.projectMain h1{
  font-size: 2rem;
  margin: 0;
  padding: 0;
  font-weight: 500;
  color: var(--text-main);
  font-family: "Roboto","Helvetica","Arial",sans-serif;
}
.projectMain a{
  font-size: small;
  margin: 5px 1rem;
  padding: 0;
}
.carousel .control-dots {
  display: none;
  position: absolute;
  bottom: 0;
  margin: 10px 0;
  padding: 0;
  text-align: center;
  width: 100%;
  z-index: 1;
}



.carousel a {
    text-transform: uppercase;
    color: var(--text-main);
    text-decoration: none;
    letter-spacing: 0.15em;
    display: inline-block;
    position: relative;
  }
  
  .carousel a:after {
    background: none repeat scroll 0 0 transparent;
    bottom: 1px;
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    background: gold;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
  }
  .carousel a:hover:after {
    width: 100%;
    left: 0;
  }
  .carousel a:hover {
    color: gold;
  }

  .detailsBtn{
    align-items: center;
    -webkit-box-pack: center;
    justify-content: center;
    position: fixed;
    box-sizing: border-box;
    outline: 0;
    top: 0;
    border: 0;
    margin-top: 5rem;
    cursor: pointer;
    user-select: none;
    vertical-align: middle;
    font-family: "Roboto","Helvetica","Arial",sans-serif;
    font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.75;
    letter-spacing: 0.02857em;
    text-transform: uppercase;
    min-width: 60px;
    padding: 5px 8px;
    border-radius: 4px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    color: var(--text-main);
    background-color: var(--bg-main);
  }
 
  @media screen and (max-width: 600px){
    .carousel-root {
      width: 80vw;
      height: auto; 

      margin: 1vh auto;
  }
  .detailsBtn{
    margin-top: 0;
  }

  
  .carousel .control-dots .dot {
    background: #fff;
    border-radius: 50%;
    display: inline-block;
    height: 8px;
    margin: 0 4px;
    padding: 0;
    transition: opacity 200ms ease-in-out;
    width: 8px;
  }
  .carousel .control-dots .dot.selected,
  .carousel .control-dots .dot:hover {
    opacity: 0.75;
  }
  .carousel .control-dots .dot.selected:hover {
    opacity: 1;
  }
  .carousel .control-dots .dot:focus {
    box-shadow: none;
    outline: none;
  }
  .carousel .control-dots .dot.active {
    background: #fff;
  }
  .carousel .control-dots .dot.active:hover {
    opacity: 1;
  }
  .carousel .control-dots .dot.active:focus {
    opacity: 1;
  }
  .carousel .control-dots .dot:focus {
    outline: none;
  }
  .carousel.carousel-slider .control-arrow{
    margin-top: 40px;
  }
.projectMain h1{
  font-size: 1.5rem;
  margin: 0;
  padding: 0;
  font-weight: 500;
  color: var(--text-main);
  font-family: "Roboto","Helvetica","Arial",sans-serif;


}
.projectMain a{
  font-size: x-small;
  margin: 5px 1rem;
  padding: 0;
}
  }